body {
  font-family: "Indie Flower", "Roboto", sans-serif;
}

.flex_container {
  height: 90vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

h1 {
  font-size: 2.4rem;
}

h1 span {
  transition: font-size 450ms;
}

h1 span:hover {
  font-size: 3.5rem;
}

.home_text {
  text-align: center;
  font-size: 1.1rem;
}

.blue_text {
  color: blue;
}

.instructions {
  margin-top: 50px;
}

.instructions ul {
  text-align: left;
  font-size: 14px;
}

.home_buttons {
  margin-top: 50px;
}

#playButton {
  padding-left: 35px;
  padding-right: 35px;
  margin-left: 30px;
}
